body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.degrees-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  height: 30px;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: stretch;
          align-items: stretch;

  padding: 5px;
  border-style: solid;
  border-width: 2px 2px 2px 2px;
  border-color: white;
}
.degrees-item {
  padding: 5px;
  margin-right: 2px;
  color: white;
}
.degrees-item:hover {
  background-color: white;
  cursor: pointer;
  color: black;
}
.active {
  background-color: white;
  cursor: pointer;
  color: black;
}

.navbar-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background-color: rgb(0, 123, 255);
  margin: 0;
}
.navbar-item {
  list-style-type: none;
  margin: 12px;
}

.city-name {
  font-weight: bold;
  font-size: 1.3rem;
  color: white;
}

.today-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  background-color: rgb(29, 6, 155);
  min-height: 200px;
  color: white;
}
.date-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1.3rem;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.icon-desc-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.temp-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.temp-container .temp-text {
  font-size: 3rem;
}
.uv-index-container {
}
.weather-desc {
  margin: 10px 0px;
  padding: 10px;
  font-size: 1.3rem;
}
.high-low-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  font-size: 1.1rem;
}

.high-low-item {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 4px;
  border-radius: 2px;
  border-bottom: 3px solid #2e17fe;
  background-color: rgb(0, 123, 255);
  color: white;
  border-radius: 10px;
}

.high-low-item i {
  font-size: 1.3rem;
}

.extra-info-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  font-size: 1.1rem;
}

.extra-info-item {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding: 4px;
  margin: 4px;
  border-radius: 2px;
  border-bottom: 3px solid #2e17fe;
  background-color: rgb(0, 123, 255);
  color: white;
  border-radius: 10px;
}

.high-low-item > span,
.extra-info-item > span {
  padding: 4px;
  margin: 4px;
}

.weather-icon,
.weather-desc {
  margin: 10px 0px;
  padding: 10px;
}

.weather-icon {
  font-size: 11em;
}
.wi-direction-up,
.wi-direction-down,
.wi-humidity,
.wi-barometer,
.wi-strong-wind {
  color: white;
}

.wi-direction-up {
  color: #48c5b5;
}
.wi-direction-down {
  color: #37a72b;
}
.wi-humidity {
  color: #00acee;
}
.wi-barometer {
  color: #fdc201;
}
.wi-strong-wind {
  color: #06d79d;
}

.list-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  color: white;
  max-width: 50%;
}
.single-list-item {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  font-size: 1rem;
  -webkit-flex: 30% 1;
          flex: 30% 1;
  padding: 4px;
  margin: 4px;
  border-radius: 10px;
  border-bottom: 3px solid #2e17fe;
  background-color: rgb(0, 123, 255);
  cursor: pointer;
}
.single-list-item:hover {
  background-color: white;
  color: blue;
}

.li-info-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.single-list-item > div,
.li-info-container > div {
  padding: 4px;
}

.li-temp-text {
  -webkit-align-self: center;
          align-self: center;
  font-size: 1.3rem;
  font-weight: bold;
}

.li-weather-icon {
  -webkit-align-self: center;
          align-self: center;
  font-size: 3.2rem;
}

.li-day,
