.graph-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-width: 50%;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  color: white;
  font-size: 1.3rem;
}

.graph {
  padding: 0px 4px;
  margin-top: 4px;
}

.graph-info {
  -webkit-align-self: center;
          align-self: center;
  margin: 10px;
}

.graph-info > span {
  padding: 4px;
}

.app-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 100vh;
}
.app-list-graph {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: stretch;
          align-items: stretch;
}
body {
  background-color: rgb(29, 6, 155);
}

